<template>
    <div style="height:100%">
         <main>
             <!-- 表单主体 -->
            <section class="Register_container">
                    <h2 style="margin-top:20px">注册页面</h2>
                    <!-- 步骤条 -->
                <section class="Register_step">
                  <el-steps :active="step" finish-status="success" simple style="margin-top: 20px">
                  <el-step title="填写账号信息"></el-step>
                  <el-step title="填写个人基本信息"></el-step>
                  <el-step title="注册成功"></el-step>
                </el-steps>
                </section>
                <form class="Register_form" >
                <!-- 第一步骤 -->
                <section class="first_step" v-if="step==1">
                <!-- 学号 -->
                <section class="Register_number">
                    <span class="iconfont icon-shenfenzhenghaoma" style="font-size:24px;color:#409EFF"></span>
                    <input type="text" autocomplete="off" placeholder="学号/手机号/账号" required v-model="formData.studentId" @blur="checkStudnetId" @keyup="checkStudnetId">
                    <span class="input_error"></span>
                </section>
             
                <!-- 用户名 -->
                <section class="Register_name">
                <span class="iconfont icon-yonghu" style="color:#F56C6C"></span>
                <input type="text"  autocomplete="off" placeholder="请输入用户名" required v-model="formData.username" @blur="checkUsername" @keyup="checkUsername">
                    <span class="input_error"></span>
                </section> 
                <!-- 密码 -->
                 <section class="Register_psd">
                 <span class="iconfont icon-mimasuo" style="color:#909399"></span>
                <input type="password" autocomplete="off" placeholder="密码" required v-model="formData.password" @keyup="checkPassword" @blur="checkPassword">
                <span class="iconfont icon-zhengyan icon"  @mousedown="handleText" @mouseup="handlePsd"></span>
                <span class="input_error"></span>
                 </section>
                 <!-- 确认密码 -->
                  <section class="Register_comfirmpsd">
                 <span class="iconfont icon-querenmima" style="color:#67C23A;font-size:24px"></span>
                <input type="password" autocomplete="off" placeholder="确认密码" required v-model="formData.comfirmPassword" @click="checkComfirmPsd" @blur="checkComfirmPsd">
                <span class="iconfont icon-zhengyan icon"  @mousedown="handleComfirmText" @mouseup="handleComfirmPsd"></span>
                <span class="input_error"></span>
                 </section>
                 </section>
                 <!-- 第二步骤 -->
                 <section class="second_step" v-else-if="step==2">
                  <!-- 真实姓名 -->
                 <section class="Register_realname">
                <span class="iconfont icon-shenfenzhenghaoma" style="color:#409EFF;font-size:24px"></span>
                <input type="text"  autocomplete="off" placeholder="请输入真实姓名" required v-model="formData.realname" @blur="checkRealname">
                <span class="input_error"></span>
                </section> 
                <!--性别 -->
                 <section class="Register_sex">
                <span class="iconfont icon-xingbie" style="color:#E6A23C;font-size:20px"></span>
                <!-- <label for="sex"> <input type="radio"  required v-model="formData.sex" name="sex">男</label>
                <label for="sex"> <input type="radio"  required v-model="formData.sex" name="sex">女</label> -->
                <select name="sex" v-model="formData.sex">
                    <option value="男" selected>男生</option>
                    <option value="女">女生</option>
                </select>
                </section> 
                 <!-- 电话号码 -->
                <section class="Register_phone">
                <span class="iconfont icon-shouji" style="color:#67C23A;font-size:22px"></span>
                <input type="text"  autocomplete="off" placeholder="请输入电话号码" required v-model="formData.phone"  @blur="checkPhone">
                <span class="input_error"></span>
                </section> 
                  <!-- 进入学校时间 -->
                <section class="Register_enterSchool">
                <span class="iconfont icon-canjiagongzuoshijian" style="color:#67C23A;font-size:22px"></span>
                <input type="date"  autocomplete="off"  required v-model="formData.enterShoolTime"  @change="checkEnterTime">
                <span class="input_error"></span>
                </section> 
                <!-- 电子邮箱 -->
                <section class="Register_email">
                <span class="iconfont icon-icon-mail" style="color:#F56C6C;font-size:17px"></span>
                <input type="email"  autocomplete="off" placeholder="请输入电子邮箱" required v-model="formData.email" @blur="checkEmail">
                <span class="input_error"></span>
                </section> 
                <!-- 年级 -->
                <section class="Register_grade">
                <span class="iconfont icon-zhuanye" style="color:#E6A23C;font-size:18px"></span>
                <input type="text"  autocomplete="off" placeholder="请输入专业年级" required v-model="formData.grade" @blur="checkMajor">
                <span class="input_error"></span>
                </section> 
                <!-- 出生日期-->
                <section class="Register_birthday">
                <span class="iconfont icon-shengrix" style="color:#F56C6C;font-size:20px"></span>
                <input type="date"  autocomplete="off" placeholder="请输入生日" required v-model="formData.birthday" @change="checkBrithday">
                <span class="input_error"></span>
                </section> 
                </section>
                <!-- 第三步 -->
                <section v-else>
                    <h1>注册成功</h1>
                </section>
                </form>
                <section class="Registe_btn">
                          <el-button :disabled="disabledFlay" @click="step--">上一步</el-button>
                          <el-button type="primary" :disabled="lastFlay" @click="nextStep" :loading="loadingFlay">下一步</el-button>
                </section>
                <section class="login_btn">
                    <span>已有账号? <router-link to="/login">去登陆</router-link> </span>
                </section>   
            </section>
            <!-- 尾部 -->
            <section class="Register_footer">
                      <span>权归所有:国之重才团队&copy;</span>
            </section>
        </main>   
    </div>
</template>
<script>
import  check from "../../minxin/register";
    export default {
        mixins:[check],
        data(){
            return{
                loadingFlay:false,
                //记录步骤                
                step:1,
                //注册信息
                formData:{
                    studentId:'',
                    username:'',
                    password:'',
                    comfirmPassword:'',
                    realname:'',
                    email:'',
                    birthday:'',
                    grade:'',
                    phone:'',
                    sex:'男',
                    enterShoolTime:''
                }
            }
        },
        computed:{
            //判断当前步骤
            disabledFlay(){
                if(this.step==1){
                    return true;
                }else if(this.step>1){
                    return false;
                }
            },
            //判断是否到最后一步
            lastFlay(){
                if(this.step==3){
                    return true;
                }else{
                    return false;
                }
            }
        },
    methods:{
     
     
    },
    watch:{
       birthday:((orderV,newV)=>{
           console.log(orderV);
           console.log(newV);
       })
    },

    }
</script>

<style lang="less" scoped>
@import "../../../static/less/public/register.less";
</style>


